import React from "react"
import { graphql } from "gatsby"

export const query = graphql`
  {
    allAirtable(
      filter: { table: { eq: "Items" } }
    ) {
      nodes {
        data {
          Name
        }
      }
    }
  }
`

export default ({ data }) => (
  <div>
    <h1>Our Resumes</h1>
    <ul>
      {data.allAirtable.nodes.map(node => (
        <h1>{node.data.Name}</h1>
      ))}
    </ul>
  </div>
)
